.profile-container {
    text-align: center;
    color: white;
}

.profile-parent {
    display: flex;
    justify-content: center;
    font-family: 'Uchen', serif;
}

.profile-container > h1 {
    padding-top: 100px;
    padding-bottom: 10rem;
    font-size: 50px;
    color: white;
    text-align: center;
    font-family: 'Fredoka One', cursive;
}

.profile-details {
    padding-left: 30px;
    width: min(30rem, 30vw);
    height: 15rem;
    text-align: left;
    font-size: 1.4rem;
}


.profile-picture {
    border-radius: 50%;
    height: 380px;
    width: 380px;
}

.profile-picture-background {
    height: 100%;
    width: 100%;
    background-image: url('https://media-exp1.licdn.com/dms/image/C4E03AQGwUY6oTdpDGg/profile-displayphoto-shrink_800_800/0/1586207426822?e=1665014400&v=beta&t=NAsHP3p9uA-b_P3YkEnCaneAMzPYs7gZUoqrQgJxDMA');
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.profile-picture-background:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
}


/************ MEDIA QUERIES ***********/

@media  (max-width: 900px) {
  .profile-details {
    padding-top: 3rem;
    padding-left: 15px;
    padding-bottom: 20px;
    font-size: 4vw;
    width: 80%;
    height: 50%;
  }

  .profile-parent {
    flex-flow: column;
    align-items: center;
  }
  
  .profile-container > h1 {
    font-size: 40px;
    padding-top: 50px;
    padding-bottom: 5rem;
  }

  .profile-picture {
    max-width: 250px;
    max-height: 250px;
  }
}
